export default {
  name: "video_skeleton_list",
  props: {
    video_list: undefined,
    set_top: undefined,
    show_node: undefined,
    show_all_video: undefined,
    clientWidth: undefined,
    SET_ROW_COUNT: undefined
  },
  data() {
    return {
      video_class_name: '',
      ROW_COUNT: this.is_mobile() ? 2 : 3,
      show_video_list: undefined
    }
  },
  created() {

    if (this.show_all_video == undefined) {
      this.show_all_video = false;
    }

    this.ROW_COUNT = this.SET_ROW_COUNT;

    setTimeout(() => {
      this.$nextTick(() => {
        this.update_row_count();
        this.update_video_list();
        this.video_class_name = this.video_col_class_name();
      });
    }, 50);

    window.addEventListener('resize', () => {
      this.update_row_count();
    });
  },
  watch: {
    'ROW_COUNT': function () {
      console.log('watch ROW_COUNT change');
      this.update_video_list();
      this.$forceUpdate();
      this.video_class_name = this.video_col_class_name();
    },
  },
  methods: {
    video_col_class_name: function () {
      let class_name = 'col-' + parseInt(12 / this.ROW_COUNT);
      console.log('video_col_class_name:', class_name)
      return class_name
    },
    set_default_row_count: function () {
      if (this.is_mobile()) {
        this.ROW_COUNT = 2;
      } else {
        this.ROW_COUNT = 4;
      }
    }
    ,
    update_row_count: function () {
      var clientWidth = this.clientWidth;
      console.log("this.$refs['video_list']:", this.$refs['video_list']);
      if (!this.$refs['video_list']) {
        setTimeout(this.update_row_count, 50);
        this.set_default_row_count();
        return this.ROW_COUNT;
      }
      if (this.$refs['video_list']) {
        clientWidth = this.$refs['video_list'].clientWidth;
      }
      if (this.SET_ROW_COUNT) {
        this.ROW_COUNT = this.SET_ROW_COUNT;
      } else {
        console.log('clientWidth:', clientWidth);
        if (clientWidth && clientWidth != 0) {
          var ROW_COUNT = parseInt(clientWidth / 288);
          ROW_COUNT = ROW_COUNT < 2 ? 2 : ROW_COUNT;
          ROW_COUNT = ROW_COUNT > 6 ? 6 : ROW_COUNT;
          console.log('ROW_COUNT:', ROW_COUNT);
          this.ROW_COUNT = ROW_COUNT;
          return this.ROW_COUNT;
        }
      }
    },
    update_video_list: function () {
      this.show_video_list = this.video_list;
      if (this.video_list && !this.show_all_video) {
        var y = (this.video_list.length % this.ROW_COUNT);
        if (y != 0) {
          var list_len = this.video_list.length - y;
          this.show_video_list = this.video_list.slice(0, list_len);
        }
      }
    }
  }
}
